      <div class="feature-list">
        <div class="feature first-line">
          <i class="icon-code smaller"></i>
          <div class="text">
            <p class="keyword">简单易用</p>
            <p class="intro">开发人员只需push代码，测试环境自动部署。体验人员关注产品&需求。</p>
          </div>
        </div>
        <div class="feature first-line">
          <i class="icon-efficient"></i>
          <div class="text">
            <p class="keyword">高效率</p>
            <p class="intro">完美的实现前后端分离，后端关注自己的接口环境，前端关注静态资源环境，轻松实现联调。</p>
          </div>
        </div>
        <div class="feature first-line">
          <i class="icon-muti"></i>
          <div class="text">
            <p class="keyword">多环境</p>
            <p class="intro">无论多少需求并行开发，都不会出现环境冲突，一个分支一个环境，方便的环境切换。</p>
          </div>
        </div>
        <div class="feature">
          <i class="icon-app smaller"></i>
          <div class="text">
            <p class="keyword">功能丰富</p>
            <p class="intro">功能强大，支持多种配置规则、支持配置分享(避免写hard code)、请求抓包、请求重放、抓包分享。</p>
          </div>
        </div>
        <div class="feature">
          <i class="icon-performance"></i>
          <div class="text">
            <p class="keyword">高性能</p>
            <p class="intro">基于SCM的构建，分布式部署，充分利用开发机实现部署，节省资源。</p>
          </div>
        </div>
        <div class="feature">
          <i class="icon-link"></i>
          <div class="text">
            <p class="keyword">全链路抓包</p>
            <p class="intro">支持从前端、到node端、到后端全链路抓包，建立完整链路。</p>
          </div>
        </div>
      </div>
    </div>
    <div class='main'>
      <h2>使用场景</h2>
      <p class="lg">
        <font class="no">No</font>host 主要用于 Web & 小程序 项目开发、联调、体验、测试及抓包调试，一般用户（开发、产品、视觉、测试等）无需本地配置环境，只需在 <font class="no">No</font>host 上统一配置环境，即可通过 <font class="no">No</font>host 小圆点，直接在页面上任意切换指定测试环境，并可以登录网页进行抓包调试请求。
      </p>
